import React from 'react';
import axios from "axios";
import Movie from "./movie";
import "./App.css"

class App extends React.Component{
  state = {
    isLoading: true,
    movies: []
  };
  getMovies = async() => {
    const {data: {data: {movies}}} = await axios.get("https://yts-proxy.now.sh/list_movies.json?sort_by=rating"); // axios = a little layer on top of fetch
    this.setState({ movies, isLoading: false });  // movies:movies 
  }
  componentDidMount(){  // fetch data
    /*setTimeout(() => {
        this.setState({ isLoading: false });
    }, 6000);*/
    this.getMovies();
  }
  render(){ // when fething data finishes, rendering happens
    const { isLoading, movies } = this.state;
    return (
      <section className="container">
        {isLoading ? ( 
          <div className="loader"><span className="loader_text">Loading...</span></div>
        ) : (
          <div className="movies">
            {movies.map(movie => ( // always return sth when using map
              <Movie 
                key={movie.id} 
                id={movie.id} 
                year={movie.year} 
                title={movie.title} 
                summary={movie.summary} 
                poster={movie.medium_cover_image}
                genres={movie.genres} />
            ))}
          </div>
        )}
      </section>
    );
  }
}

export default App;
